<!-- 牌 -->
<template>
    <div class="poker" :class="[isOpen ? 'poker-open': 'poker-close', isShake ? 'shake-poker' : '']">
        <div class="poker-inside" :class="isOpen ? 'pokerin-open': 'pokerin-close'">{{ name }}</div>
    </div>
</template>

<script>
export default {
    props: {
        name: {
            type: String,
            default: ''
        }
    },
    data () {
        return {
            isOpen: false,
            isShake: false
        }
    },
    methods: {
        close () {
            this.isOpen = false
        },
        open () {
            this.isOpen = true
        },
        shake () {
            this.isShake = true
            setTimeout(() => {
                this.isShake = false
            }, 1000)
        }
    }
}
</script>

<style scoped>
.poker {
    width: 13vh;
    height: 18.5vh;
    margin-bottom: 0.5vh;
    background-image: url('./assets/back.jpg');
    background-size: 100% 100%;
    transition: all .6s;
}
.poker-inside {
    box-sizing: border-box;
    width: 100%;height: 100%;
    border-radius: 5px;
    border: 3px solid #9f492f;
    background-color: #fff;
    transition: all .4s;
    display:flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    word-break:break-all;
    word-wrap: break-word;
    overflow: hidden;
    padding: 0 5px;
    text-align: center;
}
.poker-open {
    transform: rotateY(0deg);
}
.poker-close {
    transform: rotateY(360deg);
}
.pokerin-open {
    opacity: 1
}
.pokerin-close {
    opacity: 0
}

@keyframes shake {
    10%, 90% { transform: translate3d(-1px 0, 0); }
    20%, 80% { transform: translate3d(+2px, 0, 0)}
    30%, 70% { transform: translate3d(-4px, 0, 0)}
    40%, 60% { transform: translate3d(+4px, 0, 0)}
    50% { transform: translate3d(-4px, 0, 0)}
}
.shake-poker {
    animation: shake 500ms ease-in-out infinite;
}
</style>