<template>
  <div id="app">
    <!-- 准备界面 -->
    <div class="page-ready" :class="start ? 'hidden-page' : ''">
      <div class="ready-title">
        <span>国王游戏</span>
        <div>King Game</div>
      </div>
      <button @click="startGame" style="font-size:24px;margin: 60px auto 40px auto;">Start</button>
      
      <div class="member-list">
        <div @click="deleteMember(item, i)" class="list-item" :class="item.sex==1?'male':'female'" v-for="(item, i) in memberList" :key="i">{{ item.name }}</div>
      </div>
      <button style="margin-top: 20px;" @click="openJoin">Join</button>
    </div>
    <!-- 加入弹窗 -->
    <div class="dialog-join" v-if="joinDialog">
      <div class="join-body">
        <input style="margin-bottom: 20px;" type="text" placeholder="输入姓名 player name" v-model="form.name">
        <div>
          <label style="margin-right: 20px;" @click="form.sex = 1">
            <input type="radio" :checked="form.sex == 1">
            男 Male
          </label>
          <label @click="form.sex = 0">
            <input type="radio" :checked="form.sex == 0">
            女 Female
          </label>
        </div>
        <div style="margin-top: 20px">
          <button style="margin-right: 20px" @click="joinDialog = false">NO</button>
          <button style="font-size:20px;" @click="joinAction">OK</button>
        </div>
      </div>
    </div>
    <!-- 游戏界面 -->
    <div class="page-game" :class="!start ? 'hidden-page' : ''">
      <!-- 当前详情 -->
        <div class="game-info">
          <div class="game-info-item">
            <b>King</b>
            <div>{{ info.king.name || '--' }}</div>
          </div>
          <div class="game-info-item">
            <b>Player A</b>
            <div>{{ info.a.name || '--' }}</div>
          </div>
          <div class="game-info-item">
            <b>Player B</b>
            <div>{{ info.b.name || '--' }}</div>
          </div>
          <div class="game-info-item" style="min-width:100%">
            <b>{{ info.action || '--' }}</b>
            <div>{{ info.actionEn || '--' }}</div>
          </div>
        </div>
        <!-- 列表 -->
        <div class="game-list">
          <Poker @click.native="selectPoker(item, i)" :ref="'poker' + i" :name="item.name || ''" v-for="(item, i) in pokerList" :key="i" />
        </div>
    </div>
    <!-- 行为选择弹框 -->
    <div class="dialog-action" v-if="dialogAction">
      <div class="action-body">
        <div class="action-item" @click="selectAction(item)" v-for="(item, i) in showActionList" :key="i">
          <b>{{item.action}}</b>
          <div>{{item.actionEn}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Poker from './Poker.vue'
export default {
  name: 'App',
  components: {
    Poker
  },
  data () {
    return {
      start: false, // 是否开始游戏
      joinDialog: false,
      form: { // 添加的表单
        name: '', // 名字
        sex: 0, // 性别 1-男 0-女
      },
      memberList: [ // 成员列表
        { name: 'Shaco', sex: 1 },
        { name: '胖头陀', sex: 0 },
        { name: 'See', sex: 1 },
        { name: '瘦头陀', sex: 0 },
        { name: 'Roy', sex: 1 },
        { name: '男相网红女', sex: 0 },
        { name: 'Calvin', sex: 1 },
        { name: '娇小Girl', sex: 0 },
        { name: 'Hugo', sex: 1 },
        { name: '大屁股', sex: 0 },
        { name: 'Aaron', sex: 1 },
        { name: '迪拜头牌', sex: 0 },
      ],
      cacheList: [], // 临时成员名单
      pokerList: [ // 游戏牌得列表

      ],
      info: {
        king: {},
        a: {},
        b: {},
        action: '',
        actionEn: ''
      },

      // 行为列表
      dialogAction: false,
      actionList: [
        { action: '[A] 亲一口 [B] 的脸', actionEn: "[A] kisses [B]'s face" },
        { action: '[A] 和 [B] 跳双人舞30秒', actionEn: "[A] xxxxx [B]" },
        { action: '[A] 公主抱 [B]', actionEn: "[A] aaaaaaaaa [B]" },
        { action: '[A] 徒手测量 [B] 的胸围', actionEn: "[A] sgdsdasdfadsasd [B]'s asddfg" },
        { action: '[A] 壁咚 [B]', actionEn: "[A] asdasdasdaszx [B]'s ghjghjghj" },
        { action: '[A] 大腿夹住 [B] 的大腿10秒', actionEn: "[A] fghgjhertsfafsdf [B]" },
      ],
      showActionList: []
    }
  },
  mounted () {
    // setTimeout(() => {
    //   this.openActionList()
    // }, 1000)
  },
  methods: {
    // 随机数
    random (start, end) {
      return parseInt(Math.random() * end) + start
    },
    // 开始游戏
    startGame () {
      if (this.memberList.length < 4) {
        alert('至少4名玩家--At least 4 players')
        return
      }
      this.start = true

      // 游戏内的处理
      this.pokerList = this.memberList.map(() => {
        return {}
      })
      this.cacheList = JSON.parse(JSON.stringify(this.memberList))

      // 开始动画
      for (let i = 0; i < 15; i++) {
        setTimeout(() => {
          const index = this.random(0, this.pokerList.length)
          const ref = 'poker' + index
          this.$refs[ref][0].shake()
        }, i * 150)
      }
      setTimeout(() => {
        // 选择King
        const index = this.random(0, this.pokerList.length)
        console.log(this.memberList[index])
        const index2 = this.random(0, this.pokerList.length)
        this.pokerList[index2] = this.memberList[index]
        this.pokerList.splice(0, 0)
        const ref = 'poker' + index2
        this.$refs[ref][0].open()
        setTimeout(() => {
          this.info.king = this.memberList[index]
          this.cacheList.splice(index, 1) // 去除国王
          alert('国王是' + this.memberList[index].name + '--' + this.memberList[index].name + ' is the King')
          this.openActionList()
        }, 1000)
      }, 3000)
    },
    // 打开行为列表
    openActionList () {
      this.dialogAction = true
      for (let i = 0; i < 10; i++) {
        setTimeout(() => {
          const a = this.random(0, this.actionList.length)
          const b = this.random(0, this.actionList.length)
          const c = this.random(0, this.actionList.length)
          const d = this.random(0, this.actionList.length)
          this.showActionList = [
            this.actionList[a],
            this.actionList[b],
            this.actionList[c],
            this.actionList[d],
          ]
        }, i * 100)
      }
      setTimeout(() => {
        alert('请选择其中一个--Please choose one')
      }, 1200)
    },
    // 选择行为
    selectAction (item) {
      this.info.action = item.action
      this.info.actionEn = item.actionEn
      this.dialogAction = false
      setTimeout(() => {
        alert('请选择玩家A--Please select player A')
      }, 300)
    },
    // 选择A和B
    selectPoker (item, i) {
      console.log(item, i)
      if (item.name) return
      if (this.info.a.name) { // 选择B
        if (this.info.a.sex == 1) { // A是男的
          const girlArr = this.cacheList.filter(item => {
            return item.sex == 0
          })
          const index = this.random(0, girlArr.length)
          this.pokerList[i] = girlArr[index]
          this.pokerList.splice(0, 0)
          this.$refs['poker' + i][0].open()
          setTimeout(() => {
            this.info.b = girlArr[index]
            alert('B是' + girlArr[index].name + '--B is ' + girlArr[index].name)
            this.showResult()
          }, 1000)
        } else { // A 是女的
          const index = this.random(0, this.cacheList.length)
          this.pokerList[i] = this.cacheList[index]
          this.pokerList.splice(0, 0)
          this.$refs['poker' + i][0].open()
          setTimeout(() => {
            this.info.b = this.cacheList[index]
            alert('B是' + this.cacheList[index].name + '--B is ' + this.cacheList[index].name)
            this.showResult()
          }, 1000)
        }
      } else { // 选择A
        const index = this.random(0, this.cacheList.length)
        this.pokerList[i] = this.cacheList[index]
        this.pokerList.splice(0, 0)
        this.$refs['poker' + i][0].open()
        setTimeout(() => {
          this.info.a = this.cacheList[index]
          alert('A是' + this.cacheList[index].name + '--A is ' + this.cacheList[index].name)
          this.cacheList.splice(index, 1)
          setTimeout(() => {
            alert('请选择玩家B--Please select player B')
          }, 300)
        }, 1000)
      }
    },
    // 展示结果
    showResult () {
      setTimeout(() => {
        const action = this.info.action.replace('[A]', this.info.a.name).replace('[B]', this.info.b.name)
        const actionEn = this.info.actionEn.replace('[A]', this.info.a.name).replace('[B]', this.info.b.name)
        alert(action + '----' + actionEn)
        // 复位数据
        this.start = false
        this.info = {
          king: {},
          a: {},
          b: {},
          action: '',
          actionEn: ''
        }
        this.showActionList = []
        this.cacheList = []
        this.pokerList = []
      }, 1000)
    },
    // 打开加入弹框
    openJoin () {
      this.form = { name: '', sex: 0 }
      this.joinDialog = true
    },
    // 确认加入
    joinAction () {
      if (this.form.name == '') return
      this.memberList.push({
        name: this.form.name,
        sex: Number(this.form.sex)
      })
      this.joinDialog = false
    },
    // 删除玩家
    deleteMember (item, i) {
      const rs = confirm(`确定移除玩家 ${item.name} 吗？`)
      if (rs) {
        this.memberList.splice(i, 1)
      }
    }
  }
}
</script>

