.dialog-join, .dialog-action {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99;
}
.join-body, .action-body {
  padding: 40px 0;
  background-color: #fff;
  width: 80%;
  display: flex;
  flex-direction: column;
